*{
    margin:0;
    padding:0;
    text-decoration: none;
    font-family: "Hepta Slab", serif;
  font-optical-sizing: auto;
   
  font-style: normal;
}
   
body{
    background-color: #fff5e6;
}
.container{
    display:flex;
   
}


li {
text-decoration: none;
list-style-type: none;
}

.form {
    background: #7D0A0A;
    padding: 1em;
    border-radius: 1em;
    font-size: 2em;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
   align-items: center;
   justify-content: center;
   align-content: center;
   align-self: center;

   color: #F3EDC8;
}

input {
    height: 2em;
    width: 100%;
    padding-left: 0.5em;
    font-size: 0.7em;
    border-radius: 0.5em;
    border: 1px solid rgba(0, 0, 63, 0.37);
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.48);
    -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.48);
    -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.48);
}

.message {
    height: 5em;
    font-size: 0.5em;
}

ul {
    text-align: center;
}

ol {
    background: #7D0A0A;
    padding: 1em;
    border-radius: 1em;
    color: #F3EDC8;
}

main{
    width: 100%;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
   align-items: center;
   justify-content: center;
   max-width: 1350px;
   left: 0;
   right: 0;
   margin-left: auto;
   margin-right: auto;
   
}

.mainContent {
    color: #4e0000;
    margin-top: 5em;
    padding-top: 2em;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
  
}

.sidebar{
    color: white;
    background: #B73E3E;
    z-index: 100;
    height: 100px;
    max-width: 1350px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    transition: all 0.5s;
    width: 100%;
    position: fixed;
    top:0 ;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(2rem, 1fr)) ;
    gap: 0.5rem;
   align-items: center;
   justify-content: center;
   border-bottom-left-radius: 1em;
   border-bottom-right-radius: 1em;
}
.top_section{
    min-width: 83px;
    display: flex;
    flex-direction: row;
    align-items:center;
    padding:20px 15px;
    align-items: center;
    color: #ffffff;
    
}
.logo{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;

}
.logoFooter{
    
    display: flex;
    align-items: center;
    justify-content: center;
    

}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}
.link{
    display: flex;
    color: #040542;
    padding: 10px 15px;
    gap: 15px;
    transition: all 0.5s;
}
.link:hover{
    background:lightskyblue;
    color: #000;
    transition: all 0.5s;
}
.active{
    background: lightskyblue;
    color: #000;
}
.icon, .link_text{
    font-size: 20px;
    color: #FFF;
}

.thcLogo {
    height: 100%;
    width: 100%;
   filter: invert();


}

.heroFront {
    height: 100vh;
    width: 100vw;
    background: #cdcdcd;
    background-image: url(./assets/background2.jpg);
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    position: relative;
    z-index: 1;
}

.overlay {
    position: absolute;
    z-index: 2;
    height: 100%;
    background: rgb(130,43,0);
    background: linear-gradient(357deg, rgba(130,43,0,0.7203256302521008) 0%, rgba(167,4,106,0.6278886554621849) 45%, rgba(0,212,255,0.6643032212885154) 100%);
    width: 100%;
}

.sections1{
    margin-top: 1em;
    height: 60vh;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
    justify-items: center;
    background-image: url(./assets/heroimg.png);
    background-repeat: no-repeat;
    background-position:50% 20%;
    background-size: cover;
   
}

.contactUsSection {
    margin-top: 1em;
    height: 60vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 25em;
}

.sections{
    
    height: auto;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
    justify-items: center;
    margin-bottom: 1em;
    margin-top: 1em;
   
}
.heroContent {
    max-width: 1000px;
    display: flex;
    
    justify-content: center;
    position: relative;
    width: 100%;
    z-index: 10;
    color: #FFF;
    font-size: 2.5rem;
    text-shadow: 4px 3px 4px rgba(0, 0, 0, 0.493);
}
.divisorVertical {
    padding: 2em;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
    justify-items: center;
    width: 100%;
}

.divisor {
    display: flex;
    flex-direction: row;
    padding: 1em;
    gap: 2em;
    justify-content: center;
    align-items: center;
    justify-items: center;
    margin-top: 1em;
  
}


.divisorContainers{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.aboutUsimage {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
   margin-left: auto;
   margin-right: auto;
  
    transform: rotateY(180deg);
    border-radius: 100%;
    width: 40%;
    margin-bottom: 1em;
    border: 2px solid #7D0A0A;
}

.rightAboutUs {
    max-width: 80%;
    align-items: center;
    align-content: center;
    align-self: center;
}

.menutitle {
    font-size: 10em;
}

.menuMain {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    padding: 2em;
    border: 2px dashed #b73e3e71;
    width: 60%;
    border-radius: 1em;
    background-image: url(./assets/MENUBACKGROUND.png);
    background-repeat: no-repeat;
   background-size: 100% 100%;
    background-position: 50% 10%;
}

.rightFloatImg {
    position: absolute;
    overflow: hidden;
    border-radius: 100%;
    z-index: 100;
    height: 35vh;
   left: 0;
   top:10%;
   display: flex;
   justify-content: center;
   align-items: center;
   align-content: center;
   border: 2px dashed #DD5353;
}

.leftFloatImg {
    position: absolute;
    overflow: hidden;
    border-radius: 100%;
    z-index: 100;
    height: 35vh;
   right: -10em;
   top:60%;
   display: flex;
   justify-content: center;
   align-items: center;
   align-content: center;
   border: 2px dashed #DD5353;
}

.leftFloatImg2 {
    position: absolute;
    overflow: hidden;
    border-radius: 100%;
    z-index: 100;
    height: 35vh;
   right: 0em;
   top:105%;
   display: flex;
   justify-content: center;
   align-items: center;
   align-content: center;
   border: 2px dashed #DD5353;
}


.insideImg {
    width: 100%;
    height: 100%;
    
}

.mainTitle{
    font-family: "Sacramento", cursive;
    
    font-style: normal;
    font-weight: 900;
 }

@media screen and (max-width: 700px) and (orientation: portrait) {
    .sections {
        height: auto;
    }
    .link_text {
        font-size: 0.7em !important;
        justify-content: center;
        text-align: center;
    }
    .mainTitle {
        font-size: 3em !important;
        padding-left: 0.5em;
   
    }
    .rightAboutUs {
        font-size: 0.8rem;
        width: 100%;
        max-width: 95%;
    }
    .link {
        padding: 10px 12px;
    }

    
    .contactUsSection {
        margin-bottom: 46em !important;
    }
}

@media screen and (max-width: 855px) and (orientation: portrait){
.insideImg {
    display: none;
}

    .divisor {
        flex-direction: column;
    }

}

.right {
    text-align: center !important;
}

.footer {
    background: #B73E3E;
    margin-top: 7em;
    padding: 1em;
    height: 8em;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
   align-items: center;
   justify-content: center;
   align-content: center;
   border-top-left-radius: 1em;
   border-top-right-radius: 1em;
}

.footerConteiner {
    height: 100%;
    max-width: 350px;
}

.formButton {
    background: #EAD196;
    height: 2em;
    border-radius: 0.5em;
    color: #7D0A0A;
    font-size: 1em;
    font-weight: 900;
    text-transform:uppercase;
    transition: all ease-in-out 300ms;
    border: none;
    width: 100%;
}

.formButton:hover {
    background: #d62d2d;
    transform: translateY(-0.1em);
    border: none;
}

.emailText {
    font-size:calc( 100% * 1.7 - 6.666666669px );
}

.titles {
    font-size: 4em;
    font-family: "Sacramento", cursive;
    font-weight: 900;
    font-style: normal;
    
}

.right {
    text-align: end;
}

.textContent {
    font-size: 1em;
    background: #EDDBC0;
   
    border-radius: 1em;
}

.carousel .control-dots .dot {
    transform: scale(2);
    background: #ffd208 !important;
}

.carouselImgs {
    height: 250px;
    width:  250px;

}

.carousel-root {
    padding: 1em;
}

.sliderContainer {
    border-radius: 1em;
   text-align: start;
   align-content: center;
   background-image: url(./assets/carousel_img.jpeg);
   background-position: center;
   height: 50vh;
   
}

.aboutUsText {
    backdrop-filter: blur(4px);
   padding: 1em;
    height: 100%;
    align-items: center;
    align-self: center;
    background-color: #46000086;
    color: rgb(251, 255, 194);
    border-radius: 0.5em;
    text-shadow: 4px 4px 4px rgba(0,0,0,0.88);

}

.control-dots {
    position: absolute;

}

.carousel.carousel-slider {
    overflow:auto ;
    height: 100%;
}

.carousel .control-dots {
    bottom: 1em;
    
}

.menuMainItems {
    font-family: "Sacramento", cursive !important;
    font-weight: 900;
    font-style: normal;
    font-size: 2.5em;
}

.nameString {
    font-size: 0.5em;
}